export const cncschema = {
  properties: {
    cncSettings: {
      type: "object",
      title: "Citrix Node Controller Settings",
      required: ["cnc.nsIP", "cnc.vtepIP", "cnc.adcCredentialSecret", "cnc.network", "cnc.vxlan.id", "cnc.vxlan.port"],
      properties: {
        "cnc.enabled": { type: "boolean", title: "cnc enable", default: true },
        "cnc.image": { type: "string", title: "Citrix Node Controller Image", default: "quay.io/citrix/citrix-k8s-node-controller:2.0.0" },
        "cnc.pullPolicy": {
          type: "object", title: "Image Pull Policy",
          properties: { pullPolicy: { type: "string", default: "IfNotPresent", enum: ["Always", "IfNotPresent", "Never"] } }
        },
        'cnc.license.accept': { type: "string", title: "Accept License", default: "Yes" },
        "cnc.nsIP": { type: "string", title: "Citrix ADC IP" },
        "cnc.vtepIP": { type: "string", title: "Citrix ADC SNIP", format: "ipv4" },
        "cnc.adcCredentialSecret": { type: "string", title: "Secret Name" },
        "cnc.vxlan.id": { type: "number", title: "VxLAN ID", default: 2, minimum: 2, maximum: 16777215 },
        "cnc.vxlan.port": { type: "number", title: "VxLAN Port", minimum: 1, maximum: 65434 },
        "cnc.network": { type: "string", title: "Network" },
      },
    }
  }
};

